body {
    margin: 0;
    padding: 0;
}

header {
    position: fixed;
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    background: -webkit-linear-gradient(left, #a200ff 0%, #535df1 40%, #87e0fd 100%);
    background: linear-gradient(to right, #a200ff 0%, #535df1 40%, #87e0fd 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#87e0fd', endColorstr='#a200ff', GradientType=1);
    z-index: 999999999;
}

#btn-container {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
}

.header-btn {
    height: 100%;
    position: relative;
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.header-btn::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0;
    background-color: #262626;
    opacity: 0.15;
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transition: height 0.25s ease;
    transition: height 0.25s ease;
    z-index: 1;
}

.header-btn:hover::after{
    height: 100%;
}

.btn-link {
    display: block;
    width: 100%;
    height: 100%;
    padding-left: 25px;
    padding-right: 25px;
    text-decoration: none;
    line-height: 370%;
    color: white;
    z-index: 2;
}
#home {
